//      
window.addEventListener('load', (event) => {
    function mDisableBackButton() {
        window.history.pushState(
            null, // ,     
            "", // document.title, //    -    
            document.location //   -    
        );

        window.onpopstate = () => {
            window.history.pushState(
                null, // ,     
                "", // document.title, //    -    
                document.location //   -    
            )
        }
    }

    // ! -    ,    ,    !!!      2000,   500 -      !
    //  : https://www.youtube.com/watch?v=X-8B19HZbWk
    setTimeout(() => {
        console.log(" mDisableBackButton");
        mDisableBackButton();
    }, 3000)
})

//         ESC
window.addEventListener("keydown", (e) => {
    // console.log(' window.addEventListener "keydown", e.code= ' + e.code);
    switch (String(e.code)) {
        case "Escape": {
            GLOB_DATA.closeDialogForm();
            break;
        }
        case "NumpadEscape": {
            GLOB_DATA.closeDialogForm();
            GLOB_DATA.closeDialogForm();
            break;
        }
        default: {
            break;
        }
    }
});



